import React from 'react'
import '../styles/hero.css'
import ProfileImage from '../images/profile.png'
import { useTypewriter,Cursor } from 'react-simple-typewriter'

import BgiVideo from '../video/bg-video.mov'

function Hero() {
  const [text]=useTypewriter({
   words:['Software Developer','Web Developer','Ui Designer',''],

    typeSpeed:150,
    loop:{},
    deleteSpeed:30,
    cursorStyle:'_',
    typeStyle:{color:'white',fontSize:'2rem',fontWeight:'bold'},
    deleteStyle:{color:'white',fontSize:'2rem',fontWeight:'bold'},
    delayStyle:{color:'white',fontSize:'2rem',fontWeight:'bold'},
    cursorStyle:{color:'white',fontSize:'2rem',fontWeight:'bold'},
  });
    

  
  return (
  
    <div className='hero-container' id='home'>
 

        <div className='left-container'>
          
 
        <img className='profile-img' src={ProfileImage} alt='My Image'/>
        
    
        <h2 className='name'>Chipo Mukwavi</h2>
       <span style={{fontWeight:'bold'}}>

       <h4>Hello am a {text}</h4>
       </span>
   
        <Cursor/>
 

        <button className='hero-btn'  id="Contact">Lets Chat</button>
       
        </div>

       
    
    
    </div>
  )
}

export default Hero