import React, { useState } from 'react';

import Contact from './Contact';

function DataRow(props) {
  const { data } = props;

  const [popupData, setPopupData] = useState(null);

  const handlePopup = (item) => {
    setPopupData(item);
    console.log('I am working');
  };

  const handleClosePopup = () => {
    setPopupData(null);
  };

  return (
    <div className={`data-container ${popupData ? "blur" : ""}`}>
      <div className='data-row'>
        {data.map((item) => (
          <div key={item.id} className='data-box' onClick={() => handlePopup(item)}>
            <img className='dataimg' src={item.imageUrl} alt={item.title} />
            <h3>{item.title}</h3>
            <p>{item.description}</p>
          </div>
        ))}
      </div>

      {popupData && (
        <div className="popup-overlay">
          <div className="popup-content">
            <h1>This is a popup information</h1>
            <h1>add data component here</h1>
            <Contact/>
            <button onClick={handleClosePopup}>Close</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default DataRow;
