import React from 'react'
import '../styles/content.css'
import DataRow from './DataRow';

import Webdesign from '../images/ui-ux.png'
import Backendimg from '../images/bankend.png'
import Albumart from '../images/album art.png' 
import AppDEV from '../images/app-development.png' 
import Webdev from '../images/webdev.png' 
import Sm from '../images/SM.png' 







function Content() {
  const data = [
    { id: 1, title: 'Procurement', description: 'Laptop Provision', imageUrl: Sm },
    { id: 2, title: 'Web Development', description: 'Html, Css, Reactjs, Nextjs ', imageUrl: Webdev},
    { id: 3, title: 'Web Design', description: 'Figma,Adobe XD,Illustrator', imageUrl: Webdesign},
    { id: 4, title: 'Album Art', description: 'Adobe Products', imageUrl: Albumart  },
    { id: 5, title: 'App Development', description: 'Flutterwave', imageUrl: AppDEV},
    { id: 6, title: 'Backend Development', description: 'MongoDB,MySql', imageUrl: Backendimg },
   
    
  ];
  return (
    <div className='content-header' id="myservices">


<h2 className='content-title'>Services</h2>

      <DataRow data={data} />
   
    </div>
      
      
  )
}

export default Content;